main {
  color: #333;
  background-color: #f4f4f4;
  padding: 20px;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
  line-height: 1.6;
}

.container {
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 8px;
  max-width: 800px;
  margin: 0 auto;
  overflow: hidden;
  box-shadow: 0 0 10px #0000001a;
}

.scientist-wrapper {
  border-bottom: 1px solid #ddd;
  flex-direction: column-reverse;
  margin-bottom: 20px;
  padding: 20px;
  display: flex;
}

.scientist {
  margin-bottom: 20px;
}

.scientist-photo {
  border-radius: 50%;
  max-width: 50%;
  height: auto;
  margin: 0 auto;
  display: block;
}

.name {
  color: #1e90ff;
  margin-bottom: 0;
  font-size: 1.5em;
  font-weight: bold;
}

.research-role {
  color: #000;
  margin-top: 0;
  font-style: italic;
}

.research-areas {
  color: #555;
  margin-top: 10px;
  font-style: italic;
}

.research-contact-info {
  margin-top: 10px;
}

a {
  color: #007bff;
  text-decoration: none;
  transition: color .3s;
}

a:hover {
  color: #0056b3;
}

@media screen and (min-width: 768px) {
  .scientist-wrapper {
    flex-direction: row;
    justify-content: space-between;
  }

  .scientist-photo {
    border-radius: 40px;
    max-width: 30%;
    height: -moz-fit-content;
    height: fit-content;
    margin: 20px 0 0;
    display: block;
  }

  .scientist {
    max-width: 65%;
    margin-bottom: 20px;
  }
}

